#profilePage {
  .login-logo {
    height: 35vh;
    background-color: #18274d;
  }
  .main-container {
    position: relative;
  }

  .form-content {
    position: absolute;
    top: 30vh;
    left: 0;
    width: 90%;
    margin-left: 5%;
    border-radius: 10px;
    box-shadow: 0px 0px 19px 0px;

    .login-header {
      text-align: center;
      padding: 20px;
      font-size: 18px;
      font-weight: bold;
      color: #0e1b3d;
      hr {
        border-width: 1px;
        border-style: solid;
        width: 40px;
        background-color: #0e1b3d;
      }
    }

    .form-section {
      ion-item {
        border-style: none;
        --border-width: 0px;
      }
    }
    .login-tnc {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 20px;
      color: #8a9ead;
      line-height: 20px;

      .tnc {
        color: #ea2428;
        text-decoration: underline;
      }
    }
    .button-div {
      display: flex;
      justify-content: center;

      .button-holder {
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: -30px;
        border-radius: 30px;
        background-color: #18274d;
        color: #ffff;
        box-shadow: 0px 0px 0px 9px;
        font-size: 20px;
      }
    }
  }
}
